import React, { ReactNode, useContext, RefObject, useState } from 'react';
import { Clsx } from '../../helpers';
import { SidebarContext } from './';

type SubMenuProps = {
  children: ReactNode;
  className?: string;
  icon?: ReactNode;
  title?: string;
  defaultOpen?: boolean;
  open?: boolean;
  prefix?: ReactNode;
  suffix?: ReactNode;
  firstchild?: boolean;
  popperarrow?: boolean;
  onOpenChange?: (isOpen: boolean) => void;
  ref?: RefObject<HTMLLIElement>;
};

interface SlideDown {
  children: React.ReactNode;
  className: string;
  style?: React.CSSProperties;
  closed: boolean;
}
export const SubMenu: React.FC<SubMenuProps> = ({ children, className, icon, title, defaultOpen = false, open, prefix, suffix, firstchild, popperarrow, onOpenChange, ref, ...rest }) => {
  const { collapsed } = useContext(SidebarContext) || {};
  const [closed, setClosed] = useState(!defaultOpen);
  const handleToggleSubMenu = () => {
    if (onOpenChange) onOpenChange(closed);
    setClosed(!closed);
  };
  const subMenuRef = ref || React.createRef<HTMLLIElement>();

  return (
    <li
      ref={subMenuRef}
      className={Clsx('ui-menu-item ui-sub-menu', className, {
        open: typeof open === 'undefined' ? !closed : open,
      })}
      {...rest}>
      <div className='ui-inner-item' onClick={handleToggleSubMenu} onKeyPress={handleToggleSubMenu} role='button' tabIndex={0}>
        {icon ? (
          <span className='ui-icon-wrapper'>
            <span className='ui-icon'>{icon}</span>
          </span>
        ) : null}
        {prefix ? <span className='prefix-wrapper'>{prefix}</span> : null}
        <span className='ui-item-content'>{title}</span>
        {suffix ? <span className='suffix-wrapper'>{suffix}</span> : null}
        <span className='ui-arrow-wrapper'>
          <span className='ui-arrow'></span>
        </span>
      </div>
      {firstchild && collapsed ? (
        <div className={Clsx('ui-inner-list-item popper-element', { 'has-arrow': popperarrow })}>
          <div className='popper-inner'>
            <ul>{children}</ul>
          </div>
          {popperarrow ? <div className='popper-arrow' data-popper-arrow={true}></div> : null}
        </div>
      ) : (
        <div>
          <SlideDown closed={typeof open === 'undefined' ? closed : !open} className='ui-inner-list-item'>
            <ul>{children}</ul>
          </SlideDown>
        </div>
      )}
    </li>
  );
};
const SlideDown = (props: SlideDown) => {
  return !props.closed ? <div className={Clsx(props.className)}>{props.children}</div> : null;
};
