import React, { RefObject, useState, useEffect } from 'react';
import { Menu } from './Menu';
import { MenuItem } from './MenuItem';
import { Content } from './Content';
import { Header } from './Header';
import { SubMenu } from './SubMenu';
import { Footer } from './Footer';
import { Clsx } from '../../helpers';
interface ProSidebarProps {
  children: React.ReactNode;
  className?: string;
  width?: string;
  collapsedWidth?: string;
  collapsed?: boolean;
  rtl?: boolean;
  toggled?: boolean;
  bgImage?: string;
  breakPoint?: string;
  onToggle?: (toggled: boolean) => void;
  style?: React.CSSProperties;
  ref?: RefObject<HTMLDivElement>;
}

interface SidebarState {
  collapsed?: boolean;
  rtl?: boolean;
  toggled?: boolean;
}

export const SidebarContext = React.createContext<SidebarState>({
  collapsed: false,
  rtl: false,
  toggled: false,
});
export const Sidebar = ({ children, className, width, collapsedWidth, collapsed, rtl, toggled, bgImage, breakPoint, onToggle, style = {}, ref, ...rest }: ProSidebarProps) => {
  const [sidebarState, setSidebarState] = useState<SidebarState>({
    collapsed: typeof collapsed === 'undefined' ? false : collapsed,
    rtl: typeof rtl === 'undefined' ? false : rtl,
    toggled: typeof toggled === 'undefined' ? false : toggled,
  });

  const sidebarRef = ref || React.createRef();

  const handleToggleSidebar = () => {
    const toggleValue = sidebarState.toggled;
    setSidebarState({
      ...sidebarState,
      toggled: !toggleValue,
    });
    if (onToggle) {
      onToggle(!toggleValue);
    }
  };

  const widthStyle = width ? { width, minWidth: width } : {};
  const collapsedWidthStyle = collapsedWidth ? { width: collapsedWidth, minWidth: collapsedWidth } : {};
  const finalWidth = collapsed ? collapsedWidthStyle : widthStyle;

  useEffect(() => {
    setSidebarState({
      ...sidebarState,
      collapsed,
      rtl,
      toggled,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [collapsed, rtl, toggled]);

  return (
    <SidebarContext.Provider value={sidebarState}>
      <div
        ref={sidebarRef}
        className={Clsx('ui-sidebar', className, breakPoint, {
          collapsed,
          rtl,
          toggled,
        })}
        style={{ ...finalWidth, ...style }}
        {...rest}>
        <div className='ui-sidebar-inner'>
          {bgImage ? <img src={bgImage} alt='sidebar background' className='sidebar-bg' /> : null}
          <div className='ui-sidebar-layout'>{children}</div>
        </div>
        <div className='overlay' onClick={handleToggleSidebar} onKeyPress={handleToggleSidebar} role='button' tabIndex={0} aria-label='overlay'></div>
      </div>
    </SidebarContext.Provider>
  );
};
Sidebar.Menu = Menu;
Sidebar.MenuItem = MenuItem;
Sidebar.SubMenu = SubMenu;
Sidebar.Content = Content;
Sidebar.Header = Header;
Sidebar.Footer = Footer;
