import React, { useState } from 'react';
interface OptionProps<T> {
  value: T;
  children: React.ReactNode;
}
interface SelectProps<T> {
  children: React.ReactNode;
  value: T | null;
  onChange: (value: T | null) => void;
  showSearch?: boolean;
  onSearch?: (searchText: string) => void;
  filterOption?: boolean;
}
export const Select = <T extends string | number>({ children, value, onChange, showSearch, onSearch, filterOption }: SelectProps<T>) => {
  const [searchText, setSearchText] = useState('');
  const [filteredOptions, setFilteredOptions] = useState<React.ReactNode[]>(React.Children.toArray(children));

  const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const selectedValue = event.target.value as T | null;
    onChange(selectedValue);
  };

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newSearchText = event.target.value;
    setSearchText(newSearchText);

    if (showSearch && onSearch) {
      onSearch(newSearchText);

      if (filterOption) {
        const newFilteredOptions = React.Children.toArray(children).filter((child: any) => {
          return child.props.children.toLowerCase().includes(newSearchText.toLowerCase());
        });
        setFilteredOptions(newFilteredOptions);
      }
    }
  };

  const optionsToRender = filterOption ? filteredOptions : children;

  return (
    <div>
      <select value={value || ''} onChange={handleChange}>
        {optionsToRender}
      </select>
      {showSearch && <input type='text' value={searchText} onChange={handleSearch} />}
    </div>
  );
};
export const Option = <T extends string | number>({ value, children }: OptionProps<T>) => <option value={value}>{children}</option>;
Select.Option = Option;
