import React, { useState, useEffect, useContext, createContext } from 'react';
import { TabsContentProps, TabsContextType, TabsListProps, TabsProps, TabsTriggerProps } from '../model/tab';
import { Button } from './Button';
import { Clsx } from '../helpers';
const TabsContext = createContext<TabsContextType | undefined>(undefined);
export const Tabs = (props: TabsProps) => {
  const [activeTab, setActiveTab] = useState('');
  useEffect(() => {
    setActiveTab(props?.defaultValue ? props?.defaultValue : '1');
    // eslint-disable-next-line
  }, []);
  return (
    <TabsContext.Provider value={{ activeTab, setActiveTab }}>
      <div className={props?.className}>{props?.children}</div>
    </TabsContext.Provider>
  );
};
const TabList: React.FC<TabsListProps> = (props) => {
  return <div className={props?.className}>{props?.children}</div>;
};
const TabTrigger: React.FC<TabsTriggerProps> = (props) => {
  const { activeTab, setActiveTab } = useContext(TabsContext) || {};
  const handleClick = () => {
    if (setActiveTab) {
      setActiveTab(props?.value);
    }
  };
  return (
    <Button type='button' className={Clsx(props?.className, props?.value === activeTab && props?.activeClass ? props?.activeClass : '')} onClick={handleClick}>
      {props?.children}
    </Button>
  );
};
const TabContent: React.FC<TabsContentProps> = (props) => {
  const { activeTab } = useContext(TabsContext) || {};
  return (
    <div className={props.className} style={{ display: activeTab === props?.value ? 'block' : 'none' }}>
      {props?.children}
    </div>
  );
};

Tabs.List = TabList;
Tabs.Trigger = TabTrigger;
Tabs.Content = TabContent;
