import React from 'react';
import { Clsx } from '../helpers';
import { Button } from './Button';
interface PaginationProps {
  current: number;
  pageSize: number;
  total: number;
  onChange: (page: number) => void;
  pageRange?: number;
  activeClass?: string;
  className?: string;
  prevClass?: string;
  nextClass?: string;
  onlyNumber?: boolean;
  nextIcon?: React.ReactNode;
  prevIcon?: React.ReactNode;
}

export const Pagination: React.FC<PaginationProps> = ({ current, pageSize, total, onChange, pageRange = 7, activeClass, className, prevClass, nextClass, prevIcon, nextIcon, onlyNumber = false }) => {
  const totalPages = Math.ceil(total / pageSize);
  let startPage = current - Math.floor(pageRange / 2);
  if (startPage < 1) {
    startPage = 1;
  }
  let endPage = startPage + pageRange - 1;
  if (endPage > totalPages) {
    endPage = totalPages;
    startPage = endPage - pageRange + 1;
    if (startPage < 1) {
      startPage = 1;
    }
  }
  const handlePrev = () => {
    if (current > 1) {
      onChange(current - 1);
    }
  };

  const handleNext = () => {
    if (current < totalPages) {
      onChange(current + 1);
    }
  };
  return (
    <ul className='pagination'>
      {!onlyNumber ? (
        <li className={Clsx('pagination-prev', prevClass ? prevClass : '')}>
          <Button className='pagination-item-button' type='button' onClick={handlePrev} disabled={current === 1}>
            {prevIcon ? prevIcon : 'Previous'}
          </Button>
        </li>
      ) : null}
      {Array.from({ length: endPage - startPage + 1 }, (_, i) => startPage + i).map((page) => (
        <li key={`page-${page}`} className={Clsx('pagination-item', className ? className : '', page === current ? `pagination-item-active ${activeClass ? activeClass : ''}` : '')}>
          <Button className='pagination-item-button' type='button' onClick={() => onChange(page)}>
            {page}
          </Button>
        </li>
      ))}
      {!onlyNumber ? (
        <li className={Clsx('pagination-next', nextClass ? nextClass : '')}>
          <Button className='pagination-item-button' type='button' onClick={handleNext} disabled={current === totalPages}>
            {nextIcon ? nextIcon : 'Next'}
          </Button>
        </li>
      ) : null}
    </ul>
  );
};
