import React, { ReactNode, RefObject } from 'react';
import { Clsx } from '../../helpers';

type MenuItemProps = {
  children: ReactNode;
  className?: string;
  icon?: ReactNode;
  active?: boolean;
  prefix?: ReactNode;
  suffix?: ReactNode;
  firstchild?: number;
  popperarrow?: number;
  ref?: RefObject<HTMLLIElement>;
};

export const MenuItem: React.FC<MenuItemProps> = ({ children, className, icon, active, prefix, suffix, firstchild, popperarrow, ...rest }) => {
  const menuItemRef = rest.ref || React.createRef();

  return (
    <li ref={menuItemRef} className={Clsx('ui-menu-item', className, { active: active })} {...rest}>
      <div className='ui-inner-item' tabIndex={0} role='button'>
        {icon ? (
          <span className='ui-icon-wrapper'>
            <span className='ui-icon'>{icon}</span>
          </span>
        ) : null}
        {prefix ? <span className='prefix-wrapper'>{prefix}</span> : null}
        <span className='ui-item-content'>{children}</span>
        {suffix ? <span className='suffix-wrapper'>{suffix}</span> : null}
      </div>
    </li>
  );
};
