import React from 'react';
import { Clsx } from '../helpers';

interface BreadcrumbProps {
  children: React.ReactNode;
  className?: string;
  separator?: React.ReactNode;
}
export const BreadcrumbItem = (props: BreadcrumbProps) => {
  return (
    <li className={Clsx(props.className, 'breadcrumb-item')}>
      <span>{props.children}</span>
      {props.separator ? <span className='breadcrumb-icon'>{props.separator}</span> : null}
    </li>
  );
};
export const Breadcrumb = (props: BreadcrumbProps) => {
  const { separator = '/' } = props;
  return (
    <ol className={Clsx(props.className, 'breadcrumb')}>
      {React.Children.toArray(props?.children).map((element, index) => {
        if (!element) {
          return element;
        }
        //@ts-ignore
        return React.cloneElement(element, {
          separator: React.Children.toArray(props?.children).length - 1 === index ? '' : separator,
          key: index,
        });
      })}
    </ol>
  );
};
Breadcrumb.Item = BreadcrumbItem;
