import React, { ReactNode, RefObject } from 'react';
import { Clsx } from '../../helpers';

type HeaderProps = {
  children: ReactNode;
  className?: string;
  ref?: RefObject<HTMLDivElement>;
};

export const Header: React.FC<HeaderProps> = ({ children, className, ...rest }) => {
  const HeaderRef = rest.ref || React.createRef();

  return (
    <div ref={HeaderRef} className={Clsx('ui-sidebar-header', className)} {...rest}>
      {children}
    </div>
  );
};
