import React, { ReactNode, RefObject } from 'react';
import { Clsx } from '../../helpers';

type FooterProps = {
  children: ReactNode;
  className?: string;
  ref?: RefObject<HTMLDivElement>;
};
export const Footer: React.FC<FooterProps> = ({ children, className, ...rest }) => {
  const HeaderRef = rest.ref || React.createRef();

  return (
    <div ref={HeaderRef} className={Clsx('ui-sidebar-footer', className)} {...rest}>
      {children}
    </div>
  );
};
