import React, { ReactNode, RefObject } from 'react';
import { Clsx } from '../../helpers';

type ContentProps = {
  children: ReactNode;
  className?: string;
  ref?: RefObject<HTMLDivElement>;
};

export const Content: React.FC<ContentProps> = ({ children, className, ...rest }) => {
  const ContentRef = rest.ref || React.createRef();

  return (
    <div ref={ContentRef} className={Clsx('ui-sidebar-content', className)} {...rest}>
      {children}
    </div>
  );
};
