import React, { useState, useEffect, useContext, createContext } from 'react';
import { AccordionContextType, AccordionProps, AccordionContentProps, AccordionItemProps, AccordionTriggerProps } from '../model/accordion';
import { Clsx } from '../helpers';
import ArrowDown from '../icons/ArrowDown';

const AccordionContext = createContext<AccordionContextType | undefined>(undefined);
export const Accordion = (props: AccordionProps) => {
  const [activeTab, setActiveTab] = useState('');
  useEffect(() => {
    setActiveTab(props?.defaultValue ? props?.defaultValue : '1');
    // eslint-disable-next-line
  }, []);
  return (
    <AccordionContext.Provider value={{ activeTab, setActiveTab }}>
      <div className={props.className}>{props?.children}</div>
    </AccordionContext.Provider>
  );
};
const AccordionItem: React.FC<AccordionItemProps> = (props) => {
  const { activeTab } = useContext(AccordionContext) || {};
  return (
    <div className={Clsx(props?.className, props?.value === activeTab && props?.activeClass ? props?.activeClass : '')} aria-label={props?.value}>
      {props?.children}
    </div>
  );
};
const AccordionTrigger: React.FC<AccordionTriggerProps> = (props) => {
  const { activeTab, setActiveTab } = useContext(AccordionContext) || {};
  const handleClick = () => {
    if (setActiveTab) {
      setActiveTab(props?.value);
    }
  };
  return (
    <div className={Clsx(props?.className, props?.value === activeTab && props?.activeClass ? props?.activeClass : undefined)} onClick={handleClick}>
      {props?.children}
      <ArrowDown className={Clsx(props?.value === activeTab ? 'rotate-180' : '')} />
    </div>
  );
};
const AccordionContent: React.FC<AccordionContentProps> = (props) => {
  const { activeTab } = useContext(AccordionContext) || {};
  return (
    <div className={Clsx(props?.className, props?.value === activeTab && props?.activeClass ? props?.activeClass : '')} style={{ display: activeTab === props?.value ? 'block' : 'none' }}>
      {props?.children}
    </div>
  );
};
Accordion.Item = AccordionItem;
Accordion.Trigger = AccordionTrigger;
Accordion.Content = AccordionContent;
