import React from 'react';
import { Clsx } from '../../helpers';
type IconShape = 'square' | 'round' | 'circle';
type MenuProps = React.HTMLAttributes<HTMLElement> & {
  className?: string;
  children?: React.ReactNode;
  iconShape?: IconShape;
  popperArrow?: boolean;
  subMenuBullets?: boolean;
  innerSubMenuArrows?: boolean;
  ref?: React.RefObject<HTMLDivElement>;
};
export const Menu: React.FC<MenuProps> = ({ children, className, iconShape, popperArrow = false, subMenuBullets = false, innerSubMenuArrows = true, ref, ...rest }) => {
  const menuRef = ref || React.createRef();
  return (
    <nav
      ref={menuRef}
      className={Clsx('ui-menu', className, {
        [`shaped ${iconShape}`]: ['square', 'round', 'circle'].indexOf(iconShape ?? '') >= 0,
        'submenu-bullets': subMenuBullets,
        'inner-submenu-arrows': innerSubMenuArrows,
      })}
      {...rest}>
      <ul>
        {React.Children.toArray(children)
          .filter(Boolean)
          .map((child, index) => {
            return React.cloneElement(child as React.ReactElement, {
              key: index,
              firstchild: 1,
              popperarrow: popperArrow === true ? 1 : 0,
            });
          })}
      </ul>
    </nav>
  );
};
